html,
body,
ul,
li {
  padding: 0px;
  margin: 0px;
  font-family: 'Baloo Bhaina 2';
}

ul {
  list-style-type: none;
}

.content {
  display: flex;
  width: 100%;
  height: calc(100vh - 75px);
  align-items: center;
  align-self: center;
  align-content: center;
  justify-content: center;
}

@media only screen and (max-height : 1080px) {
  .content {
    height: inherit;
  }
}